/** @jsx jsx */
import { jsx, css } from '@emotion/core'
import React from 'react';
import TOC from './toc';
import { BrowserHistory } from 'history';
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react';

type leftMenuProps = {
  left: number,
  transition: boolean,
  page: string,
  hoverPage: boolean,
  toggleLeftMenu: Function,
  history: BrowserHistory;
}

export default class LeftMenu extends React.Component<leftMenuProps> {
  render() {
    const leftMenuStyle = css`
      position: fixed;
      z-index: 997;
      top: 0;
      left: ${this.props.left + "px"};
      width: 300px;
      height: calc(100% + 50px);
      background: #FFF;
      backdrop-filter: blur(15px) saturate(180%);
      border-right: 1px solid rgba(0,0,0,0.05);
      box-sizing: border-box;
      transition: width 300ms ${this.props.transition ? ", left 350ms" : ""};
      @media screen and (max-width: 500px) {
        width: 100%;
        left: ${this.props.left};
      }
      @supports (-webkit-backdrop-filter: blur(15px)) {
        background: rgba(255,255,255,0.7);
      }
      @supports (backdrop-filter: blur(15px)) {
        background: rgba(255,255,255,0.7);
      }
    `
    const innerStyle = css`
      margin-top: 40px;
      padding: 0 15px;
      height: calc(100% - 90px);
      overflow: auto;
    `

    return (
      <div css={leftMenuStyle}>
        <OverlayScrollbarsComponent css={innerStyle}>
          {this.props.page === "article" || this.props.hoverPage?
            <TOC hoverPage={this.props.hoverPage} toggleLeftMenu={this.props.toggleLeftMenu} history={this.props.history} />
          : ""}
        </OverlayScrollbarsComponent>
      </div>
    )
  }
}