/** @jsx jsx */
import { jsx, css } from '@emotion/core'
import React from 'react';
import tocbot from 'tocbot';
import { BrowserHistory } from 'history';

type props = {
  hoverPage: boolean,
  toggleLeftMenu: Function,
  history: BrowserHistory
}

export default class TOC extends React.Component<props> {
  render() {
    const tocStyle = css`
      margin-left: 10px;
      font-size: 30px;
      ol {
        margin: 15px 0;
        padding-inline-start: 20px;
      }
      li {
        margin: 10px 0;
        font-size: 0.9em;
        &::marker {
          color: rgba(0,0,0,0.5);
        }
      }
      a {
        color: #000;
        font-weight: bold;
        text-decoration: none;
      }
    `

    return(
      <div className="toc" css={tocStyle}>

      </div>
    )
  }
  componentDidMount() {
    tocbot.init({
      tocSelector: '.toc',
      contentSelector: '.article-content',
      headingSelector: 'h1, h2, h3, h4',
      hasInnerContainers: true,
      onClick: this.linkOnClick
    });
  }
  linkOnClick = (event: any) => {
    if (this.props.hoverPage) {
      event.preventDefault();
      let hoverPageInner = document.querySelector(".hover-page-inner .os-viewport");
      console.log(event.target.href);
      this.props.history.replace(event.target.href);
      let id = decodeURI(event.target.hash.replace("#", ""));
      let offsetTop = document.getElementById(id)?.offsetTop;
      if (hoverPageInner) hoverPageInner.scrollTop = offsetTop! - 40;
    }
    this.props.toggleLeftMenu();
  }
}