import React from 'react';
import Article from '../comp/article';
import Block from '../comp/block';

interface props {
  articleName: string;
}

export default class ArticlePage extends React.Component<props> {
  render() {
    return(
      <Block>
        <div>
          <Article articleName={this.props.articleName} loadOver={this.jumpToAnchor} />
        </div>
      </Block>
    )
  }
  jumpToAnchor = () => {
    let id = decodeURI(window.location.hash.replace("#", ""));
    let offsetTop = document.getElementById(id)?.offsetTop;
    let viewport = document.querySelector("body > .os-padding > .os-viewport");
    if (viewport) viewport.scrollTop = offsetTop!;
  }
}